.header {
  margin-left: auto;
  margin-right: auto;

  // max-width: 320px;
  min-height: 230px;

  background-image: url(../images/bg-header-mobile.jpg);
  background-color: $bg-color;

  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-device-pixel-ratio: 2),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/bg-header-mobile@2x.jpg);
  }

  @media screen and (min-width: $bp-tablet) {
    // max-width: 768px;
    padding-left: 75px;
    padding-right: 75px;
    background-image: url(../images/bg-header-tablet.jpg);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/bg-header-tablet@2x.jpg);
    }
  }

  @media screen and (min-width: $bp-desktop-m-w) {
    // max-width: 1024px;
    padding-left: 71px;
    padding-right: 71px;
    background-image: url(../images/bg-header-desktop.jpg);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/bg-header-desktop@2x.jpg);
    }
  }
}

.header-section {
  padding-top: 58px;
}

.side-nav__header {
  display: flex;
  justify-content: flex-end;

  margin-bottom: 40px;

  font-family: Roboto, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  text-transform: uppercase;

  color: $primary-white-color;
}

.header_link {
  position: absolute;
  top: 46 px;
  text-decoration: none;
  cursor: pointer;

  @media screen and (min-width: $bp-tablet) {
    display: flex;
    align-items: baseline;
  }
}

.logo-img {
  @media screen and (min-width: $bp-tablet) {
    margin-right: 10px;
  }
}

.logo__header {
  font-family: Roboto, sans-serif;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.16;
  color: $primary-white-color;

  @media screen and (max-width: $bp-mobile-max) {
    display: none;
  }
}

.btn__header {
  &:first-child {
    margin-right: 39px;
  }
}

.btn__header {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  color: $primary-white-color;
}

.btn-text {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 40 px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $primary-white-color;
}

