.search-form {
  position: relative;
  text-align: center;
}

.input_header {
  width: 100%;
  font-size: 14px;
  line-height: 1.14;
  background-color: initial;
  border: transparent;
  border-bottom: 1px solid $primary-white-color;
  font-family: Roboto, sans-serif;
  color: $primary-white-color;
  transition: border-bottom 0.25s $timing-function;

  @media screen and (min-width: $bp-tablet) {
    width: 336px;
  }
}

.search-btn {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  background-color: initial;
  border: transparent;
  color: $primary-white-color;
  transition: color 0.25s $timing-function;

  @media screen and (min-width: $bp-tablet) {
    right: 195px;
  }

  @media screen and (min-width: $bp-desktop) {
    right: 323px;
  }
}
